import React, { Fragment } from 'react'
import myapi from '../../api'
import { Form, FormControl, Button, Modal, Label, Message } from 'tinper-bee';
import 'bee-form/build/Form.css';
import 'bee-form-control/build/FormControl.css';
import 'bee-button/build/Button.css';
import 'bee-modal/build/Modal.css';
import 'bee-message/build/Message.css';
import { withRouter } from 'react-router-dom';
const FormItem = Form.FormItem;
class Addentlist extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      allValue: "",
      lineyu: "",
      statuid: "",
      visible: false,
      verifyMsg: "",
      edid: ""
    }
  }

  componentWillReceiveProps(newProps) {
    let { id, staid } = newProps;
    let { statuid, edid } = this.state;
    if (statuid != staid || edid != id) {
      this.setState({
        statuid: staid,
        edid: id
      })
      fetch(myapi.BASE_URL + `/market/requirement/manage/detail/${id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        credentials: "include"
      })
        .then(res => res.json())
        .then((response) => {
          if (response.status === 1) {
            var allCarouse = response.data
            var ssly = allCarouse.domain.domainName
            this.setState({
              allValue: allCarouse,
              lineyu: ssly
            })
          }
          else {
            Message.create({ content: response.msg, color: 'danger' });
          }
        })
    }
  }

  handleKeyPress = (e) => {
    this.setState({
      verifyMsg: e
    })
  };


  showModal() {
    this.setState({
      visible: true
    });
  };

  hideModal = () => {
    this.setState({
      visible: false,
    });
  };

  styleBtn(event) {
    if (event === 3) {
      return (
        <Button colors="primary"></Button>
      )
    } else if (event === 4) {
      return (
        <Button colors="primary"></Button>
      )
    } else {
      return (<div>
        <Button colors="secondary" style={{ marginRight: 8 }} onClick={this.showModal.bind(this)}></Button>
        <Button colors="primary" onClick={this.adoptMethod.bind(this)}></Button>
      </div>)
    }
  }

  /** */
  adoptMethod() {
    var ids = this.props.id
    fetch(myapi.BASE_URL + `/market/requirement/manage/approve/${ids}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      },
      credentials: "include"
    })
      .then(res => res.json())
      .then((response) => {
        if (response.status === 1) {
          Message.create({ content: '', color: 'success' });
          this.props.close();
        } else {
          Message.create({ content: response.msg, color: 'danger' });
        }
      })
  }

  /** */
  refuseMethod() {
    // var ids = this.state.edid
    // var vismess = this.state.verifyMsg
    var ids = this.props.id
    var vismess = this.state.verifyMsg
    console.log(ids, vismess)
    if (vismess === "") {
      Message.create({ content: '', color: 'info' });
      return false
    }
    fetch(myapi.BASE_URL + `/market/requirement/manage/reject`, {
      method: 'post',
      body: JSON.stringify({ id: ids, verifyMsg: vismess }),
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      },
      credentials: "include"
    })
      .then(res => res.json())
      .then((response) => {
        if (response.status === 1) {
          this.setState({
            visible: false
          })
          Message.create({ content: '', color: 'success' });
          this.hideModal()
        } else {
          Message.create({ content: response.msg, color: 'danger' });
        }
      })
  }

  render() {
    let { close, showModal } = this.props;
    this.allValue = this.state.allValue
    this.lineyu = this.state.lineyu
    this.statuid = this.state.statuid
    return (<div className="addemandlist">
      <Modal
        show={showModal}
        onHide={close}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <Form>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.projectName}</p>
            </FormItem>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.projectBudget}</p>
            </FormItem>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.projectDescribe}</p>
            </FormItem>
            <FormItem >
              <Label></Label>
              <p className="form-inline">{this.lineyu}</p>
            </FormItem>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.contactMan}</p>
            </FormItem>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.contactPhone}</p>
            </FormItem>
            <FormItem>
              <Label></Label>
              <p className="form-inline">{this.allValue.bidEndtime}</p>
            </FormItem>
          </Form>
        </Modal.Body>

        <Modal.Footer>
          {
            this.styleBtn(this.statuid)
          }
        </Modal.Footer>
      </Modal>

      {/*  */}
      <Modal
        show={this.state.visible}
        onHide={this.hideModal.bind(this)}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <FormControl
            componentClass={'textarea'}
            onChange={this.handleKeyPress}
            placeholder=""
            className="custom"
            style={{ height: 200 }}
          />
        </Modal.Body>

        <Modal.Footer>
          <Button onClick={this.hideModal.bind(this)} colors="secondary" style={{ marginRight: 8 }}></Button>
          <Button onClick={this.refuseMethod.bind(this)} colors="primary"></Button>
        </Modal.Footer>
      </Modal>
    </div>)
  }
}
export default withRouter(Addentlist)
