import { FormControl, Select, Pagination, Table, Modal, Button, Tabs } from "tinper-bee";
import React, { Fragment } from "react";
import styled from 'styled-components';
import DatePicker from 'bee-datepicker'
import "bee-form-control/build/FormControl.css";
import "bee-datepicker/build/DatePicker.css";
import "bee-button/build/Button.css";
import "bee-select/build/Select.css";
import "bee-table/build/Table.css";
import "bee-pagination/build/Pagination.css";
import "bee-tabs/build/Tabs.css";
import 'bee-modal/build/Modal.css';
import Header from "../common/Header";
import Content from "../common/Content";
import FormList from "../common/Form";
import SearchPanel from "../common/SearchPanel";
import { message, Tag, Radio, Checkbox } from 'antd';
import makeAjaxRequest from '../../util/request';
const TabPane = Tabs.TabPane;
const { RangePicker } = DatePicker;
const format = "YYYY-MM-DD";
const Option = Select.Option;

class FullReduction extends React.Component {
  state = {
    dataSource: {
      content: [],
      total: 0, // 
      items: 0, // 
      activePage: 1, // 
      pageSize: 10, // 
    },
    activeTabKey: '0',
    aaa: '',
    bbb: '',
    start_time: '',
    end_time: '',
    formData: {
      dataItem: {}
    },
    confirmItem: '',
    confirmAction: '',
    confirmTip: '',
    showConfirmModal: false,
    rejectReason: '',
  };

  columns = [
    {
      title: "",
      dataIndex: "order",
      key: "agreementNum",
      width: "5%",
    },
    {
      title: "",
      dataIndex: "productName",
      key: "productName",
      width: "20%",
    },
    {
      title: "",
      dataIndex: "isvName",
      key: "isvName",
      width: "8%",
    },
    {
      title: "",
      dataIndex: "operatorName",
      key: "operatorName",
      width: "8%",
    },
    {
      title: "",
      dataIndex: "originalPrice",
      key: "originalPrice",
      width: "8%",
    },
    { title: "", dataIndex: "discount", key: "discount", width: "15%" },
    { title: "", dataIndex: "payMode", key: "payMode", width: "8%" },
    {
      title: "",
      dataIndex: "busiAmount",
      key: "busiAmount",
      width: "8%",
    },
    {
      title: "",
      dataIndex: "commitTime",
      key: "commitTime",
      width: "20%",
      render: (value) => {
        return value ? (
          <div>
            <a></a>
            <a></a>
            <a></a>
          </div>
        ) : null;
      },
    },
  ];

  componentDidMount() {
    this.searchList();
  }

  changeDate = (moments) => {
    if (moments && moments.length > 0) {
      this.setState({
        start_time: `${moments[0].format('YYYY-MM-DD')} 00:00:00`,
        end_time: `${moments[1].format('YYYY-MM-DD')} 00:00:00`
      });
    } else {
      this.setState({ start_time: '', end_time: '' });
    }
  };

  handleChange = (type, e) => {
    this.setState({
      [type]: e,
    });
  };

  handleTabChange = (key) => {
    this.setState({ activeTabKey: key });
  }

  // 
  handleSelect = (activePage) => { // page, pageSize
    this.setState({ dataSource: { ...this.state.dataSource, activePage } }, () => {
      this.searchList();
    });
  };

  dataNumSelect = (index, value) => {
    this.setState({ dataSource: { ...this.state.dataSource, pageSize: value, activePage: 1 } }, () => {
      this.searchList();
    });
  };

  /*  */
  resetSearch() {
    this.setState({
    }, () => {
      this.searchList();
    });
  }

  /*  */
  searchList = async () => {
    try {
      const {
        dataSource
      } = this.state;
      const { activePage } = dataSource;
      const res = await makeAjaxRequest('/index/navigation/list', 'get', {
        page_num: activePage,
      });
      const data = res.data || [];
      data.forEach((item, index) => {
        item.order = (index + 1)
      })
      this.setState({
        dataSource: {
          ...this.state.dataSource,
          content: data,
          total: res.sum || 0,
          items: Math.floor((res.sum || 0) / this.state.dataSource.pageSize) + 1
        }
      });
    } catch (err) {
      message.error(err.message);
    }
  };

  /* // */
  handleTableAction = async (item, action) => {
    switch (action) {
      case 'edit': {
        this.showAdd(true, item);
        break;
      }
      case 'add': {
        this.showAdd(false);
        break;
      }
      case 'view': {
        this.showAdd(false);
        break;
      }
      case 'open': {
        this.setState({
          confirmItem: item,
          confirmAction: action,
          confirmTip: '? ',
          showConfirmModal: true,
          rejectReason: '',
        })
        break;
      }
      case 'stop': {
        this.setState({
          confirmItem: item,
          confirmAction: action,
          confirmTip: '? ',
          showConfirmModal: true,
          rejectReason: '',
        })
        break;
      }
      case 'join': {
        this.setState({
          confirmItem: item,
          confirmAction: action,
          confirmTip: '?',
          showConfirmModal: true,
          rejectReason: '',
        })
        break;
      }
      case 'reject': {
        this.setState({
          confirmItem: item,
          confirmAction: action,
          confirmTip: '',
          showConfirmModal: true,
          rejectReason: '',
        })
        break;
      }
      case 'confirm': {
        try {
          this.hideConfirmModal();
          await makeAjaxRequest('/index/recommendisv/dele', 'get', { isv_recommend_id: item.isvId });
          message.success('');
          this.searchList();
        } catch (err) {
          message.error(err.message);
        }
        break;
      }
      default: {
        break;
      }
    }
  }

  showAdd = (isEdit, item) => {
    this.setState({
      formData: {
        ...this.state.formData,
        showAddModal: true,
        title: isEdit ? '' : '',
        dataItem: item || {}
      }
    })
  }

  hideAddModal = () => {
    this.setState({
      formData: {
        ...this.state.formData,
        showAddModal: false,
      }
    })
  }

  handleFormDataChange = (type, e) => {
    if (type === 'ccc') {
      this.state.formData.dataItem.start_time = e[0] ? e[0].format('YYYY-MM-DD') : '';
      this.state.formData.dataItem.end_time = e[1] ? e[1].format('YYYY-MM-DD') : '';
    } else {
      this.setState({
        formData: {
          ...this.state.formData,
          dataItem: {
            ...this.state.formData.dataItem,
            [type]: e.target ? e.target.value : e,
          }
        }
      })
    }
  }

  handleListDataChange = (type, index, e) => {
    this.state.formData.dataItem.conditions[index][type] = e.target ? e.target.value : e;
    this.forceUpdate();
  }

  addListData = () => {
    if (!this.state.formData.dataItem.conditions) {
      this.state.formData.dataItem.conditions = []
    }
    this.state.formData.dataItem.conditions.push({});
    this.forceUpdate();
  }

  onFormCheckBoxChange = (e) => {
    this.state.formData.dataItem.checks = e;
  }

  submit = async () => {
    const { dataItem } = this.state.formData;
    const { in_id, username, navigation_url } = dataItem;
    try {
      this.hideAddModal();
      if (in_id) {
        await makeAjaxRequest('/index/navigation/editQuerSave', 'post', {
          in_id, username, navigation_url
        });
      } else {
        await makeAjaxRequest('/index/navigation/save', 'post', {
          username, navigation_url
        });
      }
      message.success('');
      this.searchList();
    } catch (err) {
      message.error(err.message);
    }
  }

  hideConfirmModal = () => {
    this.setState({
      showConfirmModal: false
    })
  }

  confirmAction = async () => {
    this.handleTableAction(this.state.deleteItem, 'confirm')
  }

  render() {
    const {
      dataSource,
      activeTabKey,
      aaa,
      bbb,
      formData,
      confirmItem,
      confirmAction,
      confirmTip,
      showConfirmModal,
      rejectReason,
    } = this.state;
    const { activePage, content, total, items } = dataSource;
    const { dataItem, showAddModal } = formData;
    return (
      <Fragment>
        <Header style={{ background: "#fff", padding: 0 }} title="" />
        <Content className={this.props.className} style={{ width: "100%", overflowX: "auto" }}>
          <Tabs
            ref="tabs"
            activeKey={activeTabKey}
            onChange={this.handleTabChange}
            tabBarStyle="upborder"
          >
            <TabPane tab="" key="0">
            </TabPane>
            <TabPane tab="" key="1">
            </TabPane>
          </Tabs>
          <SearchPanel
            search={this.searchList.bind(this, 0, 10)}
          >
            <FormList layoutOpt={{ md: 4, xs: 4 }}>
              <FormList.Item label="" labelCol={100}>
                <FormControl
                  className="search-item"
                  value={aaa}
                  onChange={this.handleChange.bind(null, "aaa")}
                />
              </FormList.Item>
              {activeTabKey === '1' && <FormList.Item label="" labelCol={100}>
                <FormControl
                  className="search-item"
                  value={aaa}
                  onChange={this.handleChange.bind(null, "aaa")}
                />
              </FormList.Item>
              }
              <FormList.Item label="" labelCol={100}>
                <Select
                  className="search-item"
                  onChange={this.handleChange.bind(null, "bbb")}
                  value={bbb}
                >
                  {[
                    { id: "0", stat: "" },
                    { id: "1", stat: "" },
                    { id: "2", stat: "" }].map((item) => (
                      <Option key={item.id} value={item.id}>
                        {item.stat}
                      </Option>
                    ))}
                </Select>
              </FormList.Item>
              <FormList.Item
                label=""
                labelCol={100}
              >
                <RangePicker
                  ref="rangePicker"
                  className="search-item"
                  placeholder={' ~ '}
                  format={format}
                  onChange={this.changeDate}
                />
              </FormList.Item>
            </FormList>
          </SearchPanel>
          {activeTabKey === '0'
            &&
            <div className='action-wrap'>
              <Button colors="primary" onClick={this.handleTableAction.bind(this, null, 'add')}></Button>
            </div>
          }
          <Table columns={this.columns} data={content} />
          <Pagination
            first
            last
            prev
            next
            maxButtons={5}
            dataNumSelect={["10"]}
            dataNum={0}
            boundaryLinks
            showJump={true}
            noBorder={true}
            activePage={activePage}
            onSelect={this.handleSelect}
            onDataNumSelect={this.dataNumSelect}
            total={total}
            items={items}
          />
          {/*  -  */}
          <Modal
            show={showAddModal}
            style={{ marginTop: '10vh' }}
          >
            <Modal.Header closeButton>
              <Modal.Title>{formData.title}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className='section-title'>
                {/* <Tag color="default"></Tag> */}
                
              </div>
              <FormList.Item label="" labelCol={100}>
                <FormControl
                  className="search-item"
                  value={dataItem.aaa}
                  onChange={this.handleFormDataChange.bind(null, "aaa")}
                  style={{ width: 250 }}
                />
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <RangePicker
                  ref="rangePicker"
                  className="search-item"
                  placeholder={' ~ '}
                  format={format}
                  onChange={this.handleFormDataChange.bind(null, "bbb")}
                />
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <RangePicker
                  ref="rangePicker"
                  className="search-item"
                  placeholder={' ~ '}
                  format={format}
                  onChange={this.handleFormDataChange.bind(null, "ccc")}
                />
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <FormControl
                  className="search-item"
                  value={dataItem.ddd}
                  onChange={this.handleFormDataChange.bind(null, "ddd")}
                  style={{ width: 250 }}
                />
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <Radio.Group
                  value={dataItem.eee}
                  onChange={this.handleFormDataChange.bind(null, "eee")}
                >
                  <Radio value='0'></Radio>
                  <Radio value='1'></Radio>
                </Radio.Group>
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <Checkbox.Group onChange={this.onFormCheckBoxChange}>
                  <Checkbox value="0">1</Checkbox>
                  <Checkbox value="1">2</Checkbox>
                </Checkbox.Group>
              </FormList.Item>
              <div className='section-title'>
                {/* <Tag color="default"></Tag> */}
                
              </div>
              <FormList.Item label="" labelCol={100}>
                <Radio.Group
                  value="0"
                >
                  <Radio value='0'></Radio>
                </Radio.Group>
              </FormList.Item>
              <FormList.Item label="" labelCol={100}>
                <Radio.Group
                  value={dataItem.fff}
                  onChange={this.handleFormDataChange.bind(null, "fff")}
                >
                  {(dataItem.conditions || [{}, {}]).map((item, index) => (
                    <Radio value={index} style={{ display: 'flex', alignItems: 'center', marginBottom: 10 }}>
                      <div style={{ display: 'flex', alignItems: 'center' }}>
                        <span></span>
                        <FormControl
                          className="search-item"
                          value={item.ggg1}
                          onChange={this.handleListDataChange.bind(null, "ggg1", index)}
                          style={{ width: 40, margin: "0 20px" }}
                        />
                        <span>, </span>
                        <span></span>
                        <FormControl
                          className="search-item"
                          value={item.ggg2}
                          onChange={this.handleListDataChange.bind(null, "ggg2", index)}
                          style={{ width: 40, margin: "0 20px" }}
                        />
                        <span>, </span>
                        <span></span>
                        <FormControl
                          className="search-item"
                          value={item.ggg3}
                          onChange={this.handleListDataChange.bind(null, "ggg3", index)}
                          style={{ width: 40, margin: "0 20px" }}
                        />
                        <span>%</span>
                      </div>
                    </Radio>
                  ))}
                </Radio.Group>
              </FormList.Item>
              <div className="add-item" style={{ marginTop: 20, paddingLeft: 50 }}>
                <a onClick={this.addListData}></a>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button onClick={this.hideAddModal} colors="secondary" style={{ marginRight: 8 }}></Button>
              <Button onClick={this.submit} colors="primary"></Button>
            </Modal.Footer>
          </Modal>
          {/*  */}
          <Modal
            show={showConfirmModal}
            style={{ marginTop: '20vh' }}
          >
            <Modal.Header closeButton>
              <Modal.Title></Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div>{confirmTip}</div>
              {confirmAction === 'reject' && <FormControl
                className="search-item"
                value={rejectReason}
                onChange={this.handleChange.bind(null, "rejectReason")}
              />}
            </Modal.Body>
            <Modal.Footer>
              <Button onClick={this.hideConfirmModal.bind(this)} colors="secondary" style={{ marginRight: 8 }}></Button>
              <Button onClick={this.confirmAction.bind(this)} colors="primary"></Button>
            </Modal.Footer>
          </Modal>
        </Content>
      </Fragment>
    );
  }
}

export default styled(FullReduction)`
.u-table .u-table-thead th {
  text-align: center;
}
.u-table .u-table-tbody td {
  text-align: center;
}
.u-table .u-table-tbody .actions .action{
  margin: 0 10px;
}
.action-wrap {
  text-align: right;
  padding: 20px;
  padding-right: 48px;
}
.section-title {
  margin-bottom: 10px;
  .ant-tag.ant-tag-has-color {
    color: #666666;
  }
}
`;
